<template>
  <div class="shipping_orders">
    <div class="top-search">
      
      <div class="h1">
    Shipment Onboarding
    <Alert v-if="alert" v-bind:message="alert"></Alert>
    </div>
        <div class="h2 blue">Scan newly arrived packages from suppliers into trace</div>

    <input class="order-search" type="text"  @focus="boxFocus()" @blur="boxFocus()" @keyup="boxFocus()" name="tracking_number" id="tracking_number" placeholder="Tracking Number" /> <span>OR</span>
    <input class="order-search" type="text"  @focus="boxFocus()" @blur="boxFocus()" @keyup="boxFocus()" name="ifa_id" id="ifa_id" placeholder="IFA ID" /> <span>OR</span> 
    <input class="order-search" type="text"  @focus="boxFocus()" @blur="boxFocus()" @keyup="boxFocus()" name="po_number" id="po_number" placeholder="PO Number" />
    <button v-on:click="fetchShipment">Locate Incoming Order</button><br><br>


    </div>
    

  
    <div class="incoming-order-details hide">
      <div class="h2">IFA Details</div>
<div class="ifa-left">
  <span>IFA ID: {{ incoming_order[0].ifa_id }} </span>
  <span>Estimated Arrival: {{ incoming_order[0].estimated_arrival }}</span><br>
  <span>PO_Number: {{ incoming_order[0].po_number }}</span>
  <span>Tracking Number:  {{ incoming_order[0].tracking_number }}</span><br>
  <span>Status: {{ incoming_order[0].status }} </span>

</div>
<div class="ifa-right">
      <div class="package-arrived">
         Package Arrived <br>  <input  v-on:click="confirmPackage()"  type="checkbox" id="checkbox" v-model="package_checked">
     </div>

      <div class="items-logged">
        <span class="logged"></span>
        of
        <span class="total-assets">{{ table_products.quantity }}</span> Scanned<br>
        Shipment Complete  <br><input  type="checkbox" id="checkbox" v-model="this.ifa_boarded">
      </div>
      
  


</div>
 
      

      
      
     

    

    <table id="table_id2" class="display onboarding hide" vonce >
            <thead>
                <tr>
                    <th class="select-heading heading"><input type="checkbox" data-name="" data-id="" class="select-all"></th>
                    <th class="name-heading heading">Product Name</th>
                    <th class="website-heading heading">Variants</th>
                    <th class="email-address-heading heading">Unit Price</th>
                    <th class="email-address-heading heading">Quantity(scanned)</th>
                    <th class="postcode-heading heading">Boarding Status</th>
                    <th class="view-heading heading">&nbsp;</th>
                </tr>
            </thead>
            
            <tbody> 
              <tr  class="ifaSelect">
                  <td class="table-entry select"><input type="checkbox" data-name="" data-id="" class="select-box"></td>
                  <td class="table-entry name"> {{ table_products.product_name }}</td>
                  <td class="table-entry po_number">{{ table_products.no_of_variants }}</td>
                  <td class="table-entry unit_price">{{ table_products.unit_price }}</td>
                  <td class="table-entry total_value">{{ table_products.quantity }}</td>
                  <td class="table-entry merchant_id">
                        <div v-if="ifa_boarded">Boarded</div>
                        <div v-else>Awaiting Scan</div>
                  </td>
                  <td @click="" class="table-entry view"><button v-on:click="loadAssets" v-if="this.package_checked">Scan</button></td>
              </tr>
              <tr> 
                  <td colspan="7">
                    

                      <table id="table_id2" class="display variants-table" vonce >
                              <thead>
                                  <tr>
                                      <th class="select-heading heading"><input type="checkbox" data-name="" data-id="" class="select-all"></th>
                                      <th class="name-heading heading">Color</th>
                                      <th class="website-heading heading">Size</th>
                                      <th class="email-address-heading heading">Quantity<br>(scanned)</th>
                                      <th class="postcode-heading heading">Boarding Status</th>
                                  </tr>
                              </thead>
                              
                              <tbody> 
                                <tr v-for="table_variant in table_variants"  class="ifaSelect">
                                    <td class="table-entry select"><input type="checkbox" data-name="" data-id="" class="select-box"></td>
                                    <td class="table-entry name"> {{ table_variant.color }} </td>
                                    <td class="table-entry po_number">{{ table_variant.size }} </td>
                                    <td v-bind:data-variant-id="table_variant.variant_id" class=" table-entry load-quantity"><span></span> of {{ table_variant.quantity }} </td>
                                    <td v-bind:data-variant-id="table_variant.variant_id" class="table-entry boarding"><span></span> </td>

                                </tr>
                              </tbody>
                          </table>


                  </td>
              </tr>
            </tbody>

        </table>


</div>
<div class="overlay hide fade"></div>
<div class="scan-screen hide fade">
  
      <button v-on:click="toggleQRPrint">Print All QR Codes</button>
      <button v-on:click="hideAssets">Close Window</button><br><br>



   <table id="table_id2" class="display scan-table" vonce >
      <thead>
          <tr>
             
              <th class="name-heading heading">Color</th>
              <th class="name-heading heading">Size</th>
              <th class="website-heading heading">Scanned?</th>
              <th class="website-heading heading">&nbsp;</th>
             
          </tr>
      </thead>
      
      <tbody> 
        <tr v-for="loaded_asset in loaded_assets" class="ifaSelect">
           
            <td class="table-entry name"> {{ loaded_asset.color }}</td>
            <td class="table-entry name"> {{ loaded_asset.size }}</td>
            <td class="table-entry name"> {{ loaded_asset.scanned }}</td>
            <td class="table-entry po_number">  {{ loaded_asset.asset_id }}</td>

            
        </tr>
      </tbody>
  </table>

<!-- 
  <div class="scan-item" v-for="loaded_asset in loaded_assets" >
    <span>{{ loaded_asset.asset_id }}</span>
    <span>Scanned? {{ loaded_asset.scanned }}</span>
  </div>
                                   
 -->

</div>
<div class="qr-codes"  >
  
        <button v-on:click="toggleQRPrint">Close</button><br><br>

    <div class="qr-item" v-for="loaded_asset in loaded_assets">
      
      Color: {{ loaded_asset.color }}<br>
      Size: {{ loaded_asset.size }}<br>
      Package ID: 
      <span class="asset_id">
      {{ loaded_asset.asset_id }}&{{ incoming_order[0].ifa_id }}&{{ loaded_asset.variant_id }}
      </span>
      <div id="qr-code" class="code-holder"></div>

    </div>


</div>




</div>

  </div>
</template>

<script>
  import Alert from './Alert';

  export default {
    name: 'shipping_orders',
    data () {
      return {
      orderSearch:'',
      searchMode:'',
      timer:'',
      ifa_boarded:false,
      incoming_order :[{

          ifa_id : "",
          po_number : "",
          tracking_number : "",
          estimated_arrival : "",
          status : "",
          package_arrived:false,

      }],
      table_products:[],
      table_variants:[],
      loadedIFA:[],
      loaded_assets:[],
      loaded_totals:[],
      package_checked:false,
      filterInput:'', //create the variable for us,
      alert:''
      }
    },
    methods:{


      boxFocus(type){

        if( $("#"+event.target.id).val().length > 1){ }
        $('.order-search').not("#"+event.target.id).val('');

        this.orderSearch = $("#"+event.target.id).val();
        this.searchMode = event.target.id;

        console.log(this.orderSearch + " via " + this.searchMode);

      },
      loadSelectedIFA(ifa_id){


        console.log(123);

        this.$http.get('http://35.204.120.215/trace-api/public/incoming_order/get_live_ifa/'+this.incoming_order[0].ifa_id )
          .then(function(response){


                this.loadedIFA = response.body;
                console.log(this.loadedIFA['data']['ifa']);
                console.log(this.incoming_order[0]);

                //check if package arrived
                if(this.loadedIFA['data']['ifa']['arrived_at'] == null){
                this.package_checked = false;
                }else{
                this.package_checked = true;
                }

                //load in product information
                this.table_products.no_of_variants = this.loadedIFA['data']['ifa']['order_item'].length;
                this.table_products.product_name = this.loadedIFA['data']['ifa']['name'];
                this.table_products.unit_price = this.loadedIFA['data']['ifa']['unit_price'];
                this.table_products.quantity = this.loadedIFA['data']['ifa']['quantity'];




                //--method of dealing with scenario if something doesn't comeback
                  $('#table_id2,.incoming-order-details').removeClass("hide");
                  // this.dataTable = $('#table_id2').DataTable({ });

          });



        //load in variants from database
          this.$http.get('http://35.204.120.215/trace-api/public/item/'+this.incoming_order[0].ifa_id+'/variants/')
          .then(function(response2){

                this.table_variants = response2.body;
                console.log(this.table_variants);
                console.log('http://35.204.120.215/trace-api/public/item/'+this.incoming_order[0].ifa_id+'/variants/');

          });



          console.log(this.table_products);
      },
      fetchShipment(searchMode,orderSearch){

          this.incoming_order = [{

            ifa_id : "",
            po_number : "",
            tracking_number : "",
            estimated_arrival : "",
            status : "",
            package_arrived:false,

          }];

console.log("url:"+'http://35.204.120.215/trace-api/public/incoming_order/'+this.searchMode+'/'+this.orderSearch);
          this.$http.get('http://35.204.120.215/trace-api/public/incoming_order/'+this.searchMode+'/'+this.orderSearch)
          .then(function(response){
                //console.log(response.body);
                this.incoming_order = response.body;
                //console.log(this.incoming_order[0]);
                this.loadSelectedIFA();

                //if(this.incoming_order[0].pac)
                this.refreshTotals();
                this.timer = setInterval(this.refreshTotals, 7000);

                //--method of dealing with scenario if something doesn't comeback
          });
      },
      confirmPackage(){
           console.log("confirming package");
          let new_incoming_order = {

              ifa_id : this.incoming_order[0].ifa_id

          }

          this.$http.post('http://35.204.120.215/trace-api/public/confirm-package', new_incoming_order).then(


          //--add alerts for if IFA already exists - add constraint to slim side as well
            function(response){
              console.log(response);
              this.loadSelectedIFA();
                   // this.$router.push({ path: '/', query:{alert: "incoming_order Added"} });
            }); 
      },
      acceptPackage(ifa_id){

         


          this.$http.get('http://35.204.120.215/trace-api/public/incoming_order/'+this.searchMode+'/'+this.orderSearch)
          .then(function(response){
                //console.log(response.body);
                this.incoming_order = response.body;
                //console.log(this.incoming_order[0]);
                this.loadSelectedIFA();

                //if(this.incoming_order[0].pac)

                //--method of dealing with scenario if something doesn't comeback
          });


      }, 
      refreshAssets(){

          this.$http.get('http://35.204.120.215/trace-api/public/assets/'+this.incoming_order[0].ifa_id)
          .then(function(response){
          console.log('http://35.204.120.215/trace-api/public/assets/'+this.incoming_order[0].ifa_id);
          this.loaded_assets = response.body;
          console.log(this.loaded_assets);
        });





      },
      refreshTotals(){

          this.$http.get('http://35.204.120.215/trace-api/public/assets-scanned/'+this.incoming_order[0].ifa_id)
          .then(function(response){
          console.log('http://35.204.120.215/trace-api/public/assets-scanned/'+this.incoming_order[0].ifa_id);


          this.loaded_totals = response.body;
          console.log("Loaded Totals");
          console.log(this.loaded_totals.variants);

          var q = 0;
          while(q < this.loaded_totals.variants.length ){


            console.log("div[data-variant-id='"+this.loaded_totals.variants[q].variant_id+"'] span");

            $("td[data-variant-id='"+this.loaded_totals.variants[q].variant_id+"'].load-quantity span").text(this.loaded_totals.variants[q].count);


            if(this.loaded_totals.variants[q].boarded){
            $("td[data-variant-id='"+this.loaded_totals.variants[q].variant_id+"'].boarding span").text("Boarded");

            }else{
            $("td[data-variant-id='"+this.loaded_totals.variants[q].variant_id+"'].boarding span").text("Awaiting Scan");


            }
            q++;

          }

            $(".items-logged .logged").text(this.loaded_totals.scanned_total);

            if(this.loaded_totals.IFAboarded){
             this.ifa_boarded = true; 
            }


        });



      },
      hideAssets(){
        $('.scan-screen,.overlay').addClass("fade");
                  var s = setTimeout(function(){
                  $('.scan-screen,.overlay').addClass("hide");
                  },1000);
      },
      loadAssets(){

         
            if(!this.package_arrived == true){


    
                      this.$http.get('http://35.204.120.215/trace-api/public/assets/'+this.incoming_order[0].ifa_id)
                      .then(function(response){
                            console.log('http://35.204.120.215/trace-api/public/assets/'+this.incoming_order[0].ifa_id);
                            this.loaded_assets = response.body;
                            console.log(this.loaded_assets);
                           // this.loadSelectedIFA();
                  $('.scan-screen,.overlay').removeClass("hide");
                  var s = setTimeout(function(){
                  $('.scan-screen,.overlay').removeClass("fade");
                  },1000);
                 

                            //if(this.incoming_order[0].pac)

                            //--method of dealing with scenario if something doesn't comeback

                              var s = setTimeout(function(){



                                  //load QR codes for printing
                                  $( ".qr-item" ).each(function( index ) {

                                        $(this).find("#qr-code").attr('id','asset'+index);
                                        $('asset'+index).html("");
                                        //new QRCode(document.getElementById('asset'+index), $(this).find("span").text());

                                         new QRCode(document.getElementById('asset'+index), {
                                            text: $(this).find("span").text(),
                                            width: 1000,
                                            height: 1000,
                                            colorDark : "#000000",
                                            colorLight : "#ffffff",
                                            correctLevel : QRCode.CorrectLevel.H
                                        });
                                        console.log($(this).find("span").text());

                                  });



                              },2000);


                             this.timer = setInterval(this.refreshAssets, 7000);







                      });




            }


      },
      toggleQRPrint(){

          $('body').toggleClass("show-qr");


      },
      filterBy(list, value){
          return list.filter(function(shipping_order){
          return shipping_order.order_id.indexOf(value) > -1;
        });
      },

    },
    mounted:function(){
    },
    created:function(){
      
 var s = setTimeout(function(){
    $(".container").css({opacity:1});
    },1000);
    },
    updated:function(){
      //this.fetchShippingOrders();
      this.dataTable = $('#table_id').DataTable({ });



    },
    components:{
      Alert
    }
  }


</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



