<template>
  <div class="add container">
  Add Item

    <form class="form" v-on:submit="addItem">
      <input  v-model="item.product_id" name="product_id" type="text" value="223" placeholder="Product ID" /><br>
      <input  v-model="item.inventory_type" name="inventory_type" type="text" value="type" placeholder="Inventory Type" /><br>
      <input  v-model="item.ifa_id" name="ifa_id" type="text" value="2334" placeholder="IFA ID" /><br>
      <button type="submit" class="btn btn-primary">Submit</button>
    </form>



  </div>
</template>

<script>
export default {
  name: 'add',
  data () {
    return {
      item:{}
    }
  },
  methods:{
    addItem(e){
      e.preventDefault();
      console.log(this.item.product_id);
      let newItem = {

        product_id :   this.item.product_id,
        inventory_type  :  this.item.inventory_type,
        ifa_id :   this.item.ifa_id

      }

      this.$http.post('http://localhost:8888/InventoryDocker/TraceSlim/public/item', newItem).then(

        function(response){
          console.log(response);
                      this.$router.push({ path: '/', query:{alert: "Item Added"} });


        });

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



