<template>
  <div class="shipping_orders">
    <Alert v-if="alert" v-bind:message="alert"></Alert>
    <div class="shipping-holder">
    <div class="h1">
    Shipping Orders
    </div>
    <div class="h2 blue">orders sent from Commwea into Trace</div>
   <!--  <input type="text" placeholder="Filter by ID" v-model="filterInput" /> -->

 
        <table id="table_id" class="display" vonce >
            <thead>
                <tr>
                    <th class="select-heading heading"><input type="checkbox" data-name="" data-id="" class="select-all"></th>

                    <th class="website-heading heading">Blockchain ID</th>
                    <th class="email-address-heading unit heading">Commwea ID</th>
                   <!--  <th class="email-address-heading heading">Member ID</th>
                    <th class="email-address-heading heading">Merchant ID</th> -->
                    <th class="tel-heading heading">Delivery Address</th>
                    <th class="postcode-heading unit heading">Postcode / Zip</th>
                    <th class="countrycity-heading unit heading">Shipping Phase</th>
                    <th class="view-heading unit heading">&nbsp;</th>
                </tr>
            </thead>
            
            <tbody>
              
              <tr v-for="shipping_order in filterBy(shipping_orders, filterInput)" class="shipping_order">
                  <td class="table-entry select"><input type="checkbox" data-name="" data-id="" class="select-box"></td>

                  <td class="table-entry vault_id">{{shipping_order.vault_order_id}}</td>
                  <td class="table-entry commwea_id unit">{{shipping_order.commwea_order_id}}</td>
                <!--   <td class="table-entry member_id">{{shipping_order.member_id}}</td>
                  <td class="table-entry member_id">{{shipping_order.merchant_id}}</td> -->
                  <td class="table-entry delivery">{{shipping_order.delivery_address}}</td>
                  <td class="table-entry unit postcode"> {{shipping_order.post_code}}</td>
                  <td class="table-entry unit shippingphase">{{shipping_order.shipping_phase}}</td>
                  <td class="table-entry unit view"><router-link class="mini-btn" v-bind:to="'/shipping_order/'+shipping_order.order_id">View</router-link></td>
              </tr>


            </tbody>
        </table>

</div>
  </div>
</template>

<script>
  import Alert from './Alert';

  export default {
    name: 'shipping_orders',
    data () {
      return {
      shipping_orders:[], //create the variable for us,
      filterInput:'', //create the variable for us,
      alert:''
      }
    },
    methods:{

      fetchShippingOrders(){
        this.$http.get('http://35.204.120.215/trace-api/public/shipping_orders')
        .then(function(response){
              //console.log(response.body);
              this.shipping_orders = response.body;
        });
      },
      filterBy(list, value){
          return list.filter(function(shipping_order){
          return shipping_order.order_id.indexOf(value) > -1;
        });
      },

    },
    mounted:function(){
    },
    created:function(){
      this.fetchShippingOrders();
      if(this.$route.query.alert){
        this.alert = this.$route.query.alert;
      }
      var s = setTimeout(function(){ loadTables(); },1000);
 var s = setTimeout(function(){
    $(".container").css({opacity:1});
    },1000);

    }  ,
    updated:function(){
      this.fetchShippingOrders();


    },
    components:{
      Alert
    }
  }


</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



