<template>
  <div class="incoming_orders">

    <Alert v-if="alert" v-bind:message="alert"></Alert>

    <div class="incoming-holder">
            <div class="h1">Incoming Orders</div>
                <div class="h2 blue">incoming orders made by Trace staff to suppliers</div>



          <table id="table_id" class="display incoming-table" vonce >
            <thead>
                <tr>
                    <th class="website-heading heading unit ">Po Number</th>
                    <th class="email-address-heading heading">IFA ID</th>
                    <th class="email-address-heading heading unit ">Tracking Number</th>
                    <th class="tel-heading heading">Date Created</th>
                    <th class="postcode-heading heading">Estimated Arrival</th>
                    <th class="postcode-heading heading unit ">Status</th>
                    <th class="view-heading heading unit ">&nbsp;</th>
                </tr>
            </thead>
            
            <tbody> 
              <tr  v-for="incoming_order in filterBy(incoming_orders, filterInput)" class="ifaSelect">
                  <td class="table-entry order_id unit">  {{incoming_order.po_number}} </td>
                  <td class="table-entry vault_id">{{incoming_order.ifa_id}}</td>
                  <td class="table-entry commwea_id unit">{{incoming_order.tracking_number}}</td>
                  <td class="table-entry member_id">{{incoming_order.date_created}}</td>
                  <td class="table-entry delivery">{{incoming_order.estimated_arrival}}</td>
                  <td class="table-entry delivery unit"> {{incoming_order.status}}</td>
                  <td  class="table-entry view unit"><router-link class="mini-btn" v-bind:to="'/incoming_order/'+incoming_order.incoming_order_id">View</router-link></td>

                                



              </tr>
            </tbody>

        </table>


    </div>

   <!--  <input type="text" placeholder="Filter by ID" v-model="filterInput" />
 -->
     



  </div>
</template>

<script>
  import Alert from './Alert';

  export default {
    name: 'incoming_orders',
    data () {
      return {
      incoming_orders:[], //create the variable for us,
      filterInput:'', //create the variable for us,
      alert:''
      }
    },
    methods:{

      fetchincoming_orders(){
        this.$http.get('http://35.204.120.215/trace-api/public/incoming_orders')
        .then(function(response){
              //alert(response.body);
              this.incoming_orders = response.body;
        });
      },
      filterBy(list, value){
          return list.filter(function(incoming_order){
          return incoming_order.incoming_order_id.indexOf(value) > -1;
        });
      },

    },
    created:function(){
      this.fetchincoming_orders();
      var s = setTimeout(function(){ loadTables(); },1000);
      if(this.$route.query.alert){
        this.alert = this.$route.query.alert;
      }
       var s = setTimeout(function(){
    $(".container").css({opacity:1});
    },1000);
            var s = setTimeout(function(){ loadTables(); },1000);

    }  ,
    updated:function(){
      this.fetchincoming_orders();
    },
    components:{
      Alert
    }
  }

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



