// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import vueResource from 'vue-resource'
import About from './components/About'
import Add from './components/Add'
import Edit from './components/Edit'
import Items from './components/items'
import ItemDetails from './components/ItemDetails'
import IncomingOrders from './components/IncomingOrders'
import IncomingOrdersDetails from './components/IncomingOrdersDetails'
import EditIncomingOrder from './components/EditIncomingOrder'
import AddIncomingOrder from './components/AddIncomingOrder'

import ShippingOrders from './components/ShippingOrders'
import ShippingOrdersDetails from './components/ShippingOrdersDetails'
import EditShippingOrder from './components/EditShippingOrder'
import AddShippingOrder from './components/AddShippingOrder'

import ShipmentOnboarding from './components/ShipmentOnboarding'

import Scanner from './components/Scanner'

Vue.use(vueResource)
Vue.use(VueRouter)

const router = new VueRouter({

	mode: 'history',
	base: __dirname,
	routes: [

		//	{path:'/',component : Add},

		
		{path:'/add',component : Add},
		{path :'/about',component : About},
		{path:'/items',component : Items},
		{path :'/item/:id',component : ItemDetails},
		{path :'/item/edit/:id',component : Edit},
		{path:'/incoming_orders',component : IncomingOrders},
		{path :'/incoming_order/:id',component : IncomingOrdersDetails},
		{path :'/add_incoming_order',component : AddIncomingOrder},
		{path :'/incoming_order/edit/:id',component : EditIncomingOrder},

		{path:'/shipping_orders',component : ShippingOrders},
		{path :'/shipping_order/:id',component : ShippingOrdersDetails},
		{path :'/add_shipping_order',component : AddShippingOrder},
		{path :'/shipping_order/edit/:id',component : EditShippingOrder},


		{path :'/shipment_onboarding/',component : ShipmentOnboarding},

		{path :'/scanner',component : Scanner}


	]

});


Vue.config.productionTip = false

/* eslint-disable no-new */
window.App = new Vue({
router,
components: { App },
template: `

	<div id="app">
		<header>
			
			<div class="login-info">William</div>
		</header>
		<div class="menu">
	<div class="logo"></div> <div class="trace">TRACE</div>

	<span className="divider"></span>		
			<router-link to="/">Home <div class="circle"></div></router-link>
			<router-link to="/add_incoming_order">Add Incoming Order <div class="circle"></div></router-link>
			<router-link to="/incoming_orders">Incoming Orders <div class="circle"></div></router-link>
			
			<router-link to="/shipping_orders">Commwea Orders <div class="circle"></div></router-link>
			<router-link to="/shipment_onboarding">Shipment Onboarding <div class="circle"></div></router-link>
			<router-link to="/scanner">Trace MobiScan <div class="circle"></div></router-link>

		</div>


		<div class="container">
				<router-view>
				</router-view>
		</div>

	</div>

`
}).$mount('#app')



