<template>
  <div class="details">
  	<router-link to="/">Go back</router-link>
		<h1>{{item.item_id}}</h1>
		<div class="ifa-id">{{item.ifa_id}}</div>
		<div class="product-id">{{item.product_id}}</div>
		<div class="dateadded">{{item.dateadded}}</div>

<router-link v-bind:to="'/item/edit/'+item.item_id">Edit</router-link>

		<button v-on:click="deleteItem(item.item_id)">Delete</button>
  </div>
</template>

<script>
export default {
  name: 'itemdetails',
  data () {
    return {
		item:''
    }
  },
  methods:{

      fetchItem(id){
        this.$http.get('http://localhost:8888/InventoryDocker/TraceSlim/public/item/'+id)
        .then(function(response){
              console.log(response.body);
              this.item = response.body['0'];
        });
      },
      deleteItem(id){
	      //alert(id);
        this.$http.delete('http://localhost:8888/InventoryDocker/TraceSlim/public/item/delete/'+id)
        .then(function(response){
              //alert(response.body);
              this.$router.push( {path: '/items', query: {alert:'item deleted'} } );
        });
      }

  },  
  created:function(){
	  this.fetchItem(this.$route.params.id);
	 
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



