<template>
  <div class="items">
    <h1>
    items
    <Alert v-if="alert" v-bind:message="alert"></Alert>
    </h1>
    <input type="text" placeholder="Filter by ID" v-model="filterInput" />

    <div v-for="item in filterBy(items, filterInput)" class="items">
      <div class="item">
        {{item.item_id}}
        {{item.product_id}}
        {{item.ifa_id}}
        {{item.dateadded}} 
        <router-link v-bind:to="'/item/'+item.item_id">View</router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import Alert from './Alert';

  export default {
    name: 'items',
    data () {
      return {
      items:[], //create the variable for us,
      filterInput:'', //create the variable for us,
      alert:''
      }
    },
    methods:{

      fetchitems(){
        this.$http.get('http://localhost:8888/InventoryDocker/TraceSlim/public/items')
        .then(function(response){
              //alert(response.body);
              this.items = response.body;
        });
      },
      filterBy(list, value){
        return list.filter(function(item){
        return item.item_id.indexOf(value) > -1;
      });
    },

    },
    created:function(){
      this.fetchitems();
      if(this.$route.query.alert){
        this.alert = this.$route.query.alert;
      }
    }  ,
    updated:function(){
      this.fetchitems();
    },
    components:{
      Alert
    }
  }

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



