<template>
  <div class="add container">
  Add Incoming Order

  <form class="form" v-on:submit="updateIncomingOrder">

        <input  v-model="incoming_order.ifa_id" name="ifa_id" type="text" value="" placeholder="ifa_id" /><br>
        <input  v-model="incoming_order.po_number" name="po_number" type="text" value="" placeholder="po_number" /><br>
        <input  v-model="incoming_order.tracking_number" name="tracking_number" type="text" value="" placeholder="tracking_number" /><br>
        <input  v-model="incoming_order.estimated_arrival" name="estimated_arrival" type="text" value="" placeholder="estimated_arrival" /><br>
        <input  v-model="incoming_order.status" name="status" type="text" value="" placeholder="status" /><br>
        
        <button type="submit" class="btn btn-primary">Submit</button>

    </form>


  </div>
</template>

<script>
export default {
  name: 'add',
  data () {
    return {
      incoming_order:{}
    }
  },
  methods:{

    updateIncomingOrder(e){
        e.preventDefault();
        console.log(this.incoming_order.product_id);
        let newIncomingOrder = {

          ifa_id : this.incoming_order.ifa_id, 
          po_number : this.incoming_order.po_number, 
          tracking_number : this.incoming_order.tracking_number, 
          estimated_arrival : this.incoming_order.estimated_arrival, 
          status : this.incoming_order.status

        }

        this.$http.put('http://35.204.120.215/trace-api/public/incoming_order/update/'+this.$route.params.id, newIncomingOrder).then(

          function(response){
            console.log(response);
                        this.$router.push({ path: '/', query:{alert: "Incoming Order Updated"} });


          });


    },
     fetchIncomingOrder(id){
      this.$http.get('http://35.204.120.215/trace-api/public/incoming_order/'+id)
      .then(function(response){
            console.log(response.body);
            this.incoming_order = response.body['0'];
      });
    }


  }, 
  created: function(){
     this.fetchIncomingOrder(this.$route.params.id);
      var s = setTimeout(function(){
    $(".container").css({opacity:1});
    },1000);
  }


   


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



