<template>
  <div class="details">

    <div class="order-top">
      
          <div class="h2">View Commwea Order</div>


          <div class="order-left">
          <div class="vault_order_id"><span>Blockchain Order ID:</span> {{shipping_order_details.vault_order_id}}</div>
          <div class="commwea_order_id"><span>Commwea Order ID:</span> {{shipping_order_details.commwea_order_id}}</div>
                    <div class="delivery_address"><span>Merchant ID:</span> {{shipping_order_details.merchant_id}}</div>
          <div class="member_id"><span>Member ID:</span> {{shipping_order_details.member_id}}</div>

          <div class="shipping_phase"><span>Shipping Phase</span> {{shipping_order_details.shipping_phase}}</div>
          <router-link to="/shipping_orders">Back to Orders</router-link>


          </div>
          <div class="order-right">

          <div class="post_code">Delivery Address:<br>{{shipping_order_details.delivery_address}} <br> {{shipping_order_details.post_code}}</div>
          <button id="show-prods" v-on:click=" fetchOrderItems(shipping_order_details.vault_order_id)">Show Products</button>
          <button v-on:click="shipOrder(shipping_order_details.vault_order_id)">Ship Order</button>

          </div>


    </div>
   
 
  
  <!--   <router-link v-bind:to="'/shipping_order/edit/'+shipping_order_details.order_id">Edit</router-link> -->
  
  

<div class="order-items">
  
   <table id="table_id2" class="display" vonce >
            <thead>
                <tr>

                    <th class="website-heading heading">Asset ID</th>
                    <th class="email-address-heading heading">Variant ID</th>
                    <th class="email-address-heading heading">Scan</th>
                 
                </tr>
            </thead>
            
            <tbody>
              
              <tr v-for="order_item in order_items" class="shipping_order">
                  
                  <td class="table-entry vault_id">{{order_item.asset_id}}</td>
                  <td class="table-entry commwea_id">{{order_item.variant_id}}</td>
              
                  <td class="table-entry view"><button v-on:click="showScanner();">Scan Asset ID</button></td>
              </tr>


            </tbody>
        </table>




</div>


<div class="overlay fade hide"></div>
   <div class="scanner shipment-scanner fade hide">
   

      <div class="h1">Scan to order</div>
             <!--  <button v-on:click="toggleQRPrint">Print All QR Codes</button> --><br><br>


        <input type=text size=16 placeholder="Tracking Code" class=qrcode-text><label class=qrcode-text-btn><input type=file accept="image/*" capture=environment onclick="return showQRIntro();" v-on:change="fetchOrderItems(shipping_order_details.vault_order_id)" onchange="openQRCamera2(this,window.order_id);" tabindex=-1></label> 

  </div>

  </div>
  

</template>

<style>
.qrcode-text {padding-right:1.7em; margin-right:0}
.qrcode-text-btn {display:inline-block; background:url(//dab1nmslvvntp.cloudfront.net/wp-content/uploads/2017/07/1499401426qr_icon.svg) 50% 50% no-repeat; height:1em; width:1.7em; margin-left:-1.7em; cursor:pointer}
.qrcode-text-btn > input[type=file] {position:absolute; overflow:hidden; width:1px; height:1px; opacity:0}
</style>
<script>


export default {
  name: 'shipping_order_details',
  data () {
    return {
    shipping_order_details:'',
    order_items:''
    }
  },
  methods:{

      fetchShippingOrder(id){
        this.$http.get('http://35.204.120.215/trace-api/public/shipping_order/'+id)
        .then(function(response){
              console.log(response.body);
              this.shipping_order_details = response.body['0'];
              window.order_id = this.shipping_order_details.vault_order_id;
        });
      },
      fetchOrderItems(id){
        this.$http.get('http://35.204.120.215/trace-api/public/order_items/'+id)
        .then(function(response){
              console.log(response.body);
              this.order_items = response.body;
              console.log(this.order_items);

        });
      },
      deleteShippingOrder(id){
        //alert(id);
        this.$http.delete('http://35.204.120.215/trace-api/public/shipping_order/delete/'+id)
        .then(function(response){
              //alert(response.body);
              this.$router.push( {path: '/shipping_orders', query: {alert:'shipping_order deleted'} } );
        });
      },      
      showScanner(){
     
        $('.shipment-scanner,.overlay').removeClass("hide");
        var s = setTimeout(function(){
        $('.shipment-scanner,.overlay').removeClass("fade");
        },1000);
      },
       shipOrder(order_id){
        //alert(id);
           this.$http.get('http://35.204.120.215/trace-api/public/shipping_order/dispatch/'+order_id).then(

            function(response){
              console.log(response);
                         this.$router.push({ path: '/shipping_orders', query:{alert: "shipping_order Added"} });


            });

      },
  
  },  
  created:function(){
    this.fetchShippingOrder(this.$route.params.id);
  //  this.fetchOrderItems('958ef78e5273b2a806900c4b45d0bbfe15104619f1ba769e1725315a06222b1f');
    console.log(this.$route.params.id);
    var s = setTimeout(function(){
    $(".container").css({opacity:1});
    },1000);


    var s = setTimeout(function(){
        fetchOrderItems(this.shipping_order_details.vault_order_id)
    },3000);




	 
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



