<template>
  <div class="add container">
  Add Item

    <form class="form" v-on:submit="updateItem">
      <input  v-model="item.product_id" name="product_id" type="text" value="" placeholder="Product ID" /><br>
      <input  v-model="item.inventory_type" name="inventory_type" type="text" value="" placeholder="Inventory Type" /><br>
      <input  v-model="item.ifa_id" name="ifa_id" type="text" value="" placeholder="IFA ID" /><br>
      <button type="submit" class="btn btn-primary">Submit</button>
    </form>



  </div>
</template>

<script>
export default {
  name: 'add',
  data () {
    return {
      item:{}
    }
  },
  methods:{

    updateItem(e){
        e.preventDefault();
        console.log(this.item.product_id);
        let newItem = {

          product_id :   this.item.product_id,
          inventory_type  :  this.item.inventory_type,
          ifa_id :   this.item.ifa_id

        }

        this.$http.put('http://35.204.120.215/trace-api/public/item/update/'+this.$route.params.id, newItem).then(

          function(response){
            console.log(response);
                        this.$router.push({ path: '/', query:{alert: "Item Updated"} });


          });


    },
     fetchItem(id){
      this.$http.get('http://35.204.120.215/trace-api/public/item/'+id)
      .then(function(response){
            console.log(response.body);
            this.item = response.body['0'];
      });
    }


  }, 
  created: function(){
     this.fetchItem(this.$route.params.id);
      var s = setTimeout(function(){
    $(".container").css({opacity:1});
    },1000);
  }


   


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



