<template>
  <div class="details">
  	<router-link to="/">Go back</router-link>
		<h1>{{incoming_order_details.incoming_order_details_id}}</h1>
		 <div class="incoming_order_details_id">{{incoming_order_details.incoming_order_id}}</div>
        <div class="po_number">{{incoming_order_details.po_number}}</div>
        <div class="ifa_id">{{incoming_order_details.ifa_id}}</div>
        <div class="tracking_number">{{incoming_order_details.tracking_number}}</div>
        <div class="date_created">{{incoming_order_details.date_created}}</div>
        <div class="estimated_arrival">{{incoming_order_details.estimated_arrival}}</div>
        <div class="status">{{incoming_order_details.status}}</div>

<router-link v-bind:to="'/incoming_order/edit/'+incoming_order_details.incoming_order_id">Edit</router-link>

		<button v-on:click="deleteIncomingOrder(incoming_order_details.incoming_order_id)">Delete</button>
  </div>
</template>

<script>
export default {
  name: 'incoming_order_details',
  data () {
    return {
		incoming_order_details:''
    }
  },
  methods:{

      fetchIncomingOrder(id){
        this.$http.get('http://35.204.120.215/trace-api/public/incoming_order/'+id)
        .then(function(response){
              console.log(response.body);
              this.incoming_order_details = response.body['0'];
        });
      },
      deleteIncomingOrder(id){
	      //alert(id);
        this.$http.delete('http://35.204.120.215/trace-api/public/incoming_order/delete/'+id)
        .then(function(response){
              //alert(response.body);
              this.$router.push( {path: '/incoming_orders', query: {alert:'incoming_order deleted'} } );
        });
      }

  },  
  created:function(){
	  this.fetchIncomingOrder(this.$route.params.id);
    console.log(this.$route.params.id);
	  var s = setTimeout(function(){
    $(".container").css({opacity:1});
    },1000);
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



