<template>
  <div class="add container">
  Add Shipping Order

    <form class="form" v-on:submit="addShippingOrder">

        <input  v-model="shipping_order.vault_order_id" name="vault_order_id"  type="text" value="" placeholder="vault_order_id" /><br>
        <input  v-model="shipping_order.commwea_order_id" name="commwea_order_id"  type="text" value="" placeholder="commwea_order_id" /><br>
        <input  v-model="shipping_order.member_id" name="member_id" type="text" value="" placeholder="member_id" /><br>
        <input  v-model="shipping_order.delivery_address" name="delivery_address"  type="text" value="" placeholder="delivery_address" /><br>
        <input  v-model="shipping_order.post_code" name="post_code" type="text" value="" placeholder="post_code" /><br>
        <input  v-model="shipping_order.shipping_phase" name="shipping_phase"type="text" value="" placeholder="shipping_phase" /><br>

        <button type="submit" class="btn btn-primary">Submit</button>

    </form>



  </div>
</template>

<script>
export default {
  name: 'add',
  data () {
    return {
      shipping_order:{}
    }
  },
  methods:{
    addShippingOrder(e){
      e.preventDefault();
      console.log(this.shipping_order.product_id);
      let new_shipping_order = {

          order_id : this.shipping_order.order_id,
          vault_order_id : this.shipping_order.vault_order_id,
          commwea_order_id : this.shipping_order.commwea_order_id,
          member_id : this.shipping_order.member_id,
          delivery_address : this.shipping_order.delivery_address,
          post_code : this.shipping_order.post_code,
          shipping_phase : this.shipping_order.shipping_phase

      }

      this.$http.post('http://35.204.120.215/trace-api/public/shipping_order', new_shipping_order).then(

        function(response){
          console.log(response);
                      this.$router.push({ path: '/', query:{alert: "shipping_order Added"} });


        });

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



